}
.flex-design {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
.ttip {
  position: relative;
  text-decoration: underline;
  text-decoration: underline dotted #000;
}
.ttip span {
  visibility: hidden;
  position: absolute;
  z-index: 999;
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  color: #ffffff;
  background: #000000;
  opacity: 0.9;
  bottom: 70%;
  left: 50%;
  width: 200px;
  margin-left: -100px;
  text-align: center;
}
.ttip:hover span {
  visibility: visible;
}
.ttip span:after {
  content: '';
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  margin-left: -5px;
  border-top: 5px solid #000000;
  border-right: 5px solid transparent;
  border-left: 5px solid transparent;
}
/* custom alerts */
.gmsalert {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
